@import url(https://fonts.googleapis.com/css?family=Pacifico);
html,
body {
  width: 100%;
  height: 100%;
}

body {
  background: white;
  font-family: monospace;
  font-size: 18px;
  text-align: center;
}

.logo {
  margin-top: 0px;
  margin-bottom: 10px;
  font-family: "Pacifico", cursive;
  font-size: 5rem;
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#ef5350),
    color-stop(#f48fb1),
    color-stop(#7e57c2),
    color-stop(#2196f3),
    color-stop(#26c6da),
    color-stop(#43a047),
    color-stop(#eeff41),
    color-stop(#f9a825),
    to(#ff5722)
  );
  background: linear-gradient(
    to right,
    #ef5350,
    #f48fb1,
    #7e57c2,
    #2196f3,
    #26c6da,
    #43a047,
    #eeff41,
    #f9a825,
    #ff5722
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.content {
  max-width: 320px;
  margin: 0 auto;
}

.container {
  height: 320px;
  width: 320px;
  display: flex;
  flex-wrap: wrap;
}

.pixel {
  content: " ";
  width: 16px;
  height: 16px;
  outline: 1px solid lightgrey;
  background-color: #e0e0e0;
  cursor: pointer;
}

.palette {
  height: 32px;
  outline: 1px solid white;
  display: flex;
}

.palette-color {
  content: " ";
  width: 32px;
  height: 32px;
  outline: 1px solid white;
  cursor: pointer;
}

